<template>
  
   <div class="iphone-frame">
      <div>
        <strong>Preview</strong><br>
        <span>This view will be shown in messenger</span>
      </div>
        
      <div class="preview-card">
          <div class="cover-image" :style="cardImage"></div>
          <div class="content">
              <strong>{{title}}</strong>
              <span>{{desc}}</span>
              <button v-if="firstButtonText">{{firstButtonText}}</button>
              <button v-if="secondButtonText">{{secondButtonText}}</button>
              <button v-if="thirdButtonText">{{thirdButtonText}}</button>
          </div>
      </div>
    </div>
    
</template>
<script>
export default {
  props:{
    image: String,
    title: String,
    desc: String,
    firstButtonText: String,
    secondButtonText: String,
    thirdButtonText: String
  },
  computed:{
    cardImage(){
      return {
        background: `url("${this.image}")  center / cover no-repeat`,
        color: 'red'
      }
    }
  }
}
</script>
<style lang="scss">
.iphone-frame{
  background: url("/assets/iphoneframe.png") no-repeat bottom;
  background-size: 100% 150%;
  padding: 20px 30px 40px 30px;
  display: flex;
  flex-direction: column;
  max-width: 320px;
  align-items: center;
  strong{
    font-size: 1.3em;
  }
}
@media screen and (min-width: 400px) {
  .iphone-frame{
    background-size: 100% 140%;
    padding: 20px 30px 50px 30px;
    
  }
}
.preview-card {
    display: flex;
    flex-direction: column;
    background-color: #f2f2f2;
    width: auto;
    min-width: 250px;
    max-width: 300px;
    border-radius: 1em;
    overflow: hidden;
    margin-top: 15px;
    .content {
        padding: 10px 15px;
        display: flex;
        flex-direction: column;

        strong {
            font-size: 1em;
            font-weight: bold;
            display: inline-block;
            color: #575757;
        }

        span {
            margin-top: .5rem;
            color: #575757;
            font-size: .87em;
            display: inline-block;
            word-break: break-word;
            overflow: hidden;
        }

        button {
            padding: 10px;
            border: none;
            background: #fff;
            border-radius: .75em;
            font-weight: 800;
            margin-top: .5rem;
            color: #575757;

            &:first-of-type {
                margin-top: 1rem;
            }
        }
    }
}

.cover-image {
    width: auto;
    height: 200px;
}

</style>