<template>
  <div  @click="toggle" class="d-flex justify-content-between w-100">
    <div class="mr-2">
      <span class="title">{{title}}</span><br/>
      <div class="desc">{{desc}}</div>
    </div>
      <toggle-button class="mt-1" color="#377EF9" :value="value" sync disabled
      :width="42" :height="25" :margin="2" />
  </div>
  
  
</template>
<script>

export default {
  props:{
    title: String,
    desc: String
  },
  data(){
    return{
      value: true
    }
  },
  methods:{
    toggle(){
      this.value = !this.value;
    }
  }
}
</script>
<style>
  .vue-js-switch.disabled{
    opacity: 1 !important;
  }
</style>
<style lang="scss" scoped>
.title{
  font-size: 16px;
  font-weight: 700;
  letter-spacing: 0.5px;
}
.desc{
  margin-top: 10px;
  font-size: 14px;
  font-weight: 500;
}
</style>