<template>
    <div class="iphoneframe-container">
      <BackButton @click="$emit('showAccountInfo')"/>
      <PreviewCardWithIphoneFrame
        class="w-100"
        :image="previewCardImage"
        :title="previewCardTitle"
        :desc="previewCardDesc"
        firstButtonText="Not happy"
        secondButtonText="Just Ok"
        thirdButtonText="Very happy"
      />

      <div class="mt-4 mr-5 w-99">
        <StageTitle stageNum="3" :title="stageInfoText.title" :desc="stageInfoText.desc" />
      </div>
      <div class="mt-4 w-70">
        <ThankYouMessageForm />
      </div>
      <div class="mt-4 w-100">
        <MobileToggle
          :title="toggles.askCustomerInfomation.title"
          :desc="toggles.askCustomerInfomation.desc"
        />
      </div>
    </div>
</template>

<script>
import PreviewCardWithIphoneFrame from "@/components/PreviewCardWithIphoneFrame.vue";
import BackButton from "@/components/BackButton";
import StageTitle from "@/components/StageTitle";
import MobileToggle from "@/components/MobileToggle";
import ThankYouMessageForm from "@/components/ThankYouMessageForm";
export default {
  components: {
    PreviewCardWithIphoneFrame,
    StageTitle,
    MobileToggle,
    BackButton,
    ThankYouMessageForm
  },
  data() {
    return {
      pageActive: true,
      previewCardDefaults: {
        title: "Your feedback is awesome",
        desc: "Let's us know your feedback on our service.We do appreciate it",
        image:
          "https://images.pexels.com/photos/2666467/pexels-photo-2666467.jpeg?auto=compress&cs=tinysrgb&dpr=2&w=500"
      },
      stageInfoText: {
        title: "'Thank you' message",
        desc: "Fill your contact information once"
      },
      toggles: {
        askCustomerInfomation: {
          title: "Ask Customer Information",
          desc:
            "Enable this option will ask the customer name, contact number and address."
        }
      },
      companyName: "",
      companyAddress: "",
      companyImage: "" //Both base64 and imageUrl supported
    };
  },
  computed: {
    previewCardTitle() {
      return this.companyName
        ? this.companyName
        : this.previewCardDefaults.title;
    },
    previewCardDesc() {
      return this.companyAddress
        ? this.companyAddress
        : this.previewCardDefaults.desc;
    },
    previewCardImage() {
      return this.companyImage
        ? this.companyImage
        : this.previewCardDefaults.image;
    }
  }
};
</script>
<style lang="scss">
.iphoneframe-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top:-1.5rem;
}
</style>
