<template>
  <div @click="animateWave" class="bg-white d-flex flex-center" :class="{'wave':animate}">
    <BackIcon />
  </div>
</template>
<script>
import BackIcon from '@/assets/icons/LeftChevron'
export default {
  components:{
    BackIcon
  },
  data(){
    return{
      animate: false
    }
  },
  methods:{
    animateWave(){
      this.animate = true;
      setTimeout(() => {
      this.$emit("click");  
      }, 50);
      
    }
  }
}
</script>
<style lang="scss" scoped>
div{
  position: fixed;
  left: 20px;
  top: 70px;
  border-radius: 50%;
  width: 55px;
  height: 55px;
  box-shadow: 5px 5px 8px #6868687d;
  border: .5px solid #dbdbdb7d;
  svg{
    width: 25px;
    height: auto;
  }
}
</style>