<template>
  <div class="d-flex">
    <div
      class="addPhoto"
      style="position:relative"
      v-if="uploadImageList.length > 0"
    >
      <div class="addPhoto--inside">
        <div
          class="addPhoto--photo"
          v-for="(i, index) in uploadImageList"
          :key="index"
        >
          <div
            class="addPhoto--photo-bg"
            @click="deleteUploadImage()"
            :style="{ backgroundImage: 'url(' + url(i) + ')' }"
          >
            <button
              class="addPhoto--photo-close"
              @click="deleteUploadImage(index)"
            >
              <i class="las la-times-circle" />
            </button>
          </div>
        </div>
      </div>
    </div>

    <div
      class="addPhoto"
      style="position:relative"
      v-if="uploadImageList.length < 1"
      @click="imageUpload"
    >
      <span style="font-size:1.2rem;">
        <i class="las la-plus-circle"></i>
      </span>
      <span style="font-size:0.8rem;">{{ title }}</span>
      <span class="close close-photo" style="display:none;">
        <i class="las la-times-circle"></i>
      </span>
      <input
        @change="onChange"
        type="file"
        accept="image/*"
        multiple
        style="display:none;"
        ref="fileInput"
      />
    </div>
  </div>
</template>

<script>
export default {
  props: ["title"],
  data() {
    return {
      uploadImageList: [],
    };
  },
  methods: {
    imageUpload() {
      this.$refs.fileInput.click();
    },
    onChange(e) {
      let fileList = e.target.files;
      for (let i = 0; i < fileList.length; i++) {
        let file = e.target.files[i];
        let reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = () => {
          // setTimeout(() => {
          this.uploadImageList.push(reader.result);
          // }, 0);
        };
      }
      e.target.value = null; //remove last file from input to upload same file again
    },
    deleteUploadImage() {
      this.uploadImageList = [];
    },
    url(i) {
      if (i.includes("base64")) return i;
      return "https://kktstroage.azureedge.net/yammo/smm/answerset/" + i;
    },
  },
};
</script>

<style lang="scss" scoped>
.addPhoto {
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  flex-direction: column;
  width: 100%;
  min-height: 10rem;
  background-color: #e6e4e4;
  text-align: center;
  color: black;
  font-weight: 300;
  border-top-right-radius: 10px;
  border-top-left-radius: 10px;
  &--inside {
    overflow: auto;
    white-space: nowrap;
    //margin-left: 12px;
    margin-right: 12px;
  }
  &--photo {
    border-radius: 18px;
    height: 7rem;
    width: 7rem;
    display: inline-block;
    overflow: hidden;
    position: relative;
    &:hover .addPhoto--photo-close {
      opacity: 1;
      transition: none;
    }
    &:last-child {
      margin-right: 0px;
    }
    &-bg {
      background-position: 50% 50%;
      background-repeat: no-repeat;
      height: 7rem;
      width: 7rem;
      background-size: cover;
      transform: rotate(0deg);
    }
    &-close {
      height: 20px;
      position: absolute;
      right: 2px;
      top: 8px;
      font-size: 23px;
      width: 20px;
      color: rgb(12, 12, 12);
      background-color: transparent;
      border: none;
      cursor: pointer;
      // opacity: 0;
      padding: 0;
      transition: opacity 150ms ease-out;
    }
  }
}
</style>
